.ItemSelector {
  display: flex;
  align-items: stretch; }

.ItemSelector .clear-button {
  width: 10%;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center; }

.ItemSelector .clear-button,
