  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

.DoubleSlider {
  display: flex;
  flex-wrap: wrap;
  width: 100%; }
  .DoubleSlider .track-label {
    flex: 1 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%; }
  .DoubleSlider .track-wrapper {
    flex: 1 1; }
  .DoubleSlider .Viz svg {
    height: 40px; }
  .DoubleSlider rect .handle {
    cursor: pointer; }
  .DoubleSlider rect.handle.active {
    fill: gray; }
  .DoubleSlider rect.track {
    fill: gray; }
  .DoubleSlider .label-container {
    display: flex;
    justify-content: space-between;
    padding: 0px 20px; }

@media screen and (max-width: 400px) {
  .DoubleSlider {
    display: unset; }
    .DoubleSlider .track-label {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%; }
    .DoubleSlider .track-wrapper {
      width: 100%; } }


.Viz svg {
  width: 100%;
  height: 200px;
  height: 150px; }

rect.enter {
  fill: green; }

rect.update {
  fill: orange; }

rect.exit {
  fill: red; }



